import React from 'react';
import {FormattedMessage} from 'react-intl';
import {Form} from 'react-bootstrap';
import emailjs from 'emailjs-com';
import{ init } from 'emailjs-com';
init("user_wXjYUlrNhGydzVgZJ5dHe");




export default function ContactMe() {

  function sendEmail(e) {
    e.preventDefault();

    emailjs.sendForm('service_yn6eunj', 'template_ncqf034', e.target, 'user_wXjYUlrNhGydzVgZJ5dHe')
      .then((result) => {
          console.log(result.text);
      }, (error) => {
          console.log(error.text);
      });
      e.target.reset()
  	  alert("Mensaje enviado");
  }
 return (
	<form onSubmit={sendEmail}>
		<Form.Label>
			<FormattedMessage id="user.name" defaultMessage="Name" />
		</Form.Label>
		<input type="text" name="user_name" required/>

		<Form.Label>
			<FormattedMessage id="user.email" defaultMessage="Email" />
		</Form.Label>
		<input type="email" name="user_email" placeholder="" required/>

		<Form.Label>
			<FormattedMessage id="user.number" defaultMessage="Phone Number" />
		</Form.Label>
		<input type="number" name="contact_number" placeholder="" required/>

		<Form.Label>
			<FormattedMessage id="user.message" defaultMessage="Message" />
		</Form.Label>
		<textarea name="message" />

		<button type="submit" value="Send" className="custom-btn btn">
			{" "}
			<FormattedMessage id="user.send" defaultMessage="Send" />
		</button>
	</form>
 );
}